
@font-face {
  font-family: 'Helvetica Neue Thin';
  src: url(../assets/fonts/HelveticaNeueLt.ttf);
}
@font-face {
  font-family: 'Bernard';
  src: url(../assets/fonts/Bernard.ttf);
}
@font-face {
  font-family: 'Tw';
  src: url(../assets/fonts/Tw.ttf);
}

/* header */
.header[data-v-6b1c7352] {
  height: 300px;
  /* background: linear-gradient(70deg, white, #433B38 80%, #1E0A02); */
  background: url('../../static/img/homeheader.jpg') no-repeat;
  background-size:100% 100%;
  min-width: 1080pt;
}
.sub-header[data-v-6b1c7352] {
  background-color: #2a0910;
  height: 100px;
  padding-top: 12px;
  font-family: 'Tw', CH;
  font-size: 21pt;
  letter-spacing: 4px;
  min-width: 1080pt;
}
.social-icon > img[data-v-6b1c7352] {
  padding: 10px;
  width: 40px;
  height: 40px;
  cursor: pointer;
}
.big-title[data-v-6b1c7352] {
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
}
.big-title h1[data-v-6b1c7352] {
  color: #cc141e;
  font-size: 95pt;
  font-family: Tw, CH;
  letter-spacing: 45px;
  text-align: center;
  /* height: 90%;
  width: 120%; */
}
@media screen {
.big-title  > img[data-v-6b1c7352] {
    height: 7em;
    margin: 47px;
}
}
.big-title > span[data-v-6b1c7352] {
  padding: 10px;
  color: #E43F37;
  font-size: 8.5em;
  text-shadow: black -5px 5px 5px;
}
.navigation[data-v-6b1c7352] {
  display: -ms-flexbox;
  display: flex;
  display: -webkit-flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
  width: 85%;
  margin: 0 auto;
}
.navigation > a[data-v-6b1c7352] {
  text-decoration: none;
  font-size: 1.5em;
  color: rgb(207, 45, 45);
  padding: 5px;
  margin: 0 auto;
}
.navigation > div[data-v-6b1c7352] {
  position: absolute;
  right: 10%;
  font-size: 1em;
  color: white;
  font-family:"Microsoft YaHei"
}
.search > img[data-v-6b1c7352] {
  position: relative;
  width: 30px;
  height: 30px;
  transition: 1s;
  -webkit-transition: 1s;
  -moz-transition: 1s;
  -o-transition: 1s;
  cursor: pointer;
}
.search > input[data-v-6b1c7352] {
  text-indent: 20px;
  display: none;
  position: absolute;
  top: 40px;
  left: 10px;
  width: 160px;
  border-width: 0 0 2px;
  background: none;
  transition: 1s;
  color: black;
}
.img-active[data-v-6b1c7352] {
  transform: translateX(-20px);
  -webkit-transform: translateX(-20px);
  -moz-transform: translateX(-20px);
  -o-transform: translateX(-20px);
}
.input-active[data-v-6b1c7352] {
  display: block !important;
}

/* adapt big screen */
@media screen and (min-width: 1500px){
.big-title > span[data-v-6b1c7352] {
    padding: 10px;
    font-size: 8.5em;
}

  /* news first */
.news-first > div > img[data-v-6b1c7352] {
    width: 60%;
    height: auto;
    max-height: 700px;
}
}

/* adapt small screen */
@media screen and (max-width: 1200px) {
.big-title > span[data-v-6b1c7352] {
    padding: 6px;
    font-size: 6.5em;
}
}
.logout[data-v-6b1c7352] {
  position: absolute;
  right: 20px;
  top: 40px;
}

/* For iphone 5 */
@media (min-device-width: 320px) and (max-device-width: 568px) {
.navigation[data-v-6b1c7352] {
    width: 70%;
}
.search-label[data-v-6b1c7352] {
    display: none;
}
.search > div[data-v-6b1c7352]{
    position: absolute;
    top: 25px;
    left: -610px;
    width: 400px;
}
.search > img[data-v-6b1c7352] {
    display: none;
}
a[data-v-6b1c7352] {
    font-size: 2.0em !important;
}
}

/* For ipad */
@media (min-width: 768px) and (max-width: 1024px) {
.big-title > img[data-v-6b1c7352] {
    height: 6.8em;
}
}
/* For ipad pro */
@media (min-width: 1024px) and (max-width: 1366px) {
.big-title > img[data-v-6b1c7352] {
    height: 6.8em;
}
}




